import { Component, OnInit } from '@angular/core';
import { GlobalParamServiceService } from 'src/app/global-param-service.service';
import { PlaceObj } from 'src/app/wheretostay/Place';

@Component({
  selector: 'app-wheretostay',
  templateUrl: './wheretostay.component.html',
  styleUrls: ['./wheretostay.component.css']
})
export class WheretostayComponent implements OnInit {
  places = [];
  constructor(private globalParamServiceService: GlobalParamServiceService) { }

  ngOnInit() {
    this.globalParamServiceService.emitChange('navbar-shrink');
    this.places = [
      new PlaceObj('Hotel1', 'Best Hotel in Fort Kochi','assets/img/team/1.jpg', '', '', '', ''),
      new PlaceObj('Hotel2', 'Mattancherry, Find Dutch Palace.', 'assets/img/team/2.jpg', '', '', '', ''),
      new PlaceObj('Hotel3', 'Bolghatty Island, a tourist paradise', 'assets/img/team/3.jpg', '', '', '', '')
    ];
  }

}
