import { Component } from '@angular/core';
import { GlobalParamServiceService } from 'src/app/global-param-service.service';
import { RouterOutlet } from '@angular/router';
import { slideInAnimation } from 'src/app/animation';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    slideInAnimation
    // animation triggers go here
  ]
})
export class AppComponent {
  constructor(private _globalParamServiceService: GlobalParamServiceService) { }
  siteName = 'Cochin1.com';
  shopName = 'COCHIN1';
  heading1 = 'SHOPPING';
  heading2 = 'FOOD';
  heading3 = 'What to Visit';
  heading4 = 'Where to Stay';
  heading5 = 'Contact';
  headerStyle = this._globalParamServiceService.changeEmitted$.subscribe(
    text => {
      this.headerStyle = text;
      console.log(text);
    });
  prepareRoute(outlet: RouterOutlet) {
      return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
   }
}
